import React, { useState } from "react";

import "./SouthAfricanMap.css";

import saMap from "./world-map.svg";

import "./SouthAfricanMap.css";
export default function SouthAfricanMap() {
  const [toggle, setToggle] = useState(false);

  const onToggle = () => {
    setToggle(!toggle);
  };
  return (
    <>
      {!toggle ? (
        <div className="SA-Map">
          <h4>South African Map</h4>
          <figure>
            <img src={saMap} alt="south african map" />
          </figure>
          <footer>
            <h4>Total Positive</h4>
            <h3>450</h3>
            <button onClick={onToggle}>View map</button>
          </footer>
        </div>
      ) : (
        <div className="map">
          <button onClick={onToggle}>Return</button>
        </div>
      )}
    </>
  );
}
